
import CommonHeader from '@/pages/common/header.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
name: 'Activity',
data () {
return {
show: true,

value: '2019-04-01',
activities: [],
timelineShow: true
}
},
computed: {
calendar () {
return this.$store.state.activities
}
},
methods: {
onChange (val) {
this.activities = this.$store.getters.getTodoByDate(val)
this.timelineShow = this.activities.length > 0
},
buildSlotFn (line, index, data) {
this.has = this.calendar.find((value) => {
return value.date === data.formatedDate
})

return this.has ? '<div class="circle"></div>' : ''
}
},
components: {
CommonHeader,
InlineCalendar,
dateFormat
},
mounted () {
this.$store.dispatch('getActivities')
const _this = this
setTimeout(() => {
_this.value = dateFormat(new Date(), 'YYYY-MM-DD')
}, 0)
}
}



